.header {
    width: 100%;
    background: #fff;
}

.container{
    /* max-width: 1350px;  */
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-between; 
    align-items: center;
    padding: 17px 100px;
    background: #fff;
    z-index: 100;

}
.list{
    display: flex;
    margin: 0 auto; 
    gap: 45px;  
    list-style-type: none;
    @media (max-width: 893px) {
        gap: 10px;
        font-size: 10px;
    }
}
.item{
    font-family: var(--second-family);
    font-weight: 500;
    font-size: 16px;
    color: #000000;
    @media (max-width: 834px) {
    font-size: 10px;
}
}
.item a{
    text-decoration: none;
    color: #000000;
    cursor: pointer;
}
.user{
    display: flex;
    flex-direction: row;
    justify-content: center; 
    align-items: center;
    gap: 25px;
    @media (max-width: 834px) {
        gap: 18px;
    }
    @media (max-width: 393px) {
        flex-direction: column-reverse;
        
    }
}
    .logo{
    width: 82px;
    height: 56px;
    @media (max-width: 834px) {
        width: 34px;
        height: 22px;
}
    }
.logo img{
    width: 100%;
    height: 100%;
    object-fit: contain;
}
.wrapper{
    display: flex;
    flex-direction: row;
    justify-content: center; 
    align-items: center;
    gap: 111px;
    
    transition: max-height 0.3s ease;
}
.user_img{
    width: 50px;
    height: 50px;
    @media (max-width: 834px) {
        width: 40px;
        height: 40px;
}
}
.menuToggle {
    display: none;
    font-size: 28px;
    background: none;
    border: none;
    cursor: pointer;
    overflow: hidden;
}
@media (max-width: 393px) {

    /* Гамбургер показать */
    .menuToggle {
        display: block;
    }

    /* Прячем десктопное меню */
    .list {
        display: flex;
        flex-direction: column;
        gap: 25px;
        align-items: left;
    }

    .rotated {
    transform: rotate(-90deg);
    transition: 0,3s ease;
}

    /* Оболочка меню закрыта */
    .wrapper {
        position: absolute;
        top: 60px;
        right: 0;
        background: white;
        width: 100%; 
        height: 146px;
        padding: 0 30px 0 0;
        /* box-shadow: 0 3px 10px rgba(0,0,0,0.1); */
        max-height: 0;
        overflow: hidden;
        background: #e6e6e6;
    }

    /* Открытое меню */
    .open {
        max-height: 300px;
    }
}