.backdrop {
  position: fixed;
  inset: 0;
  background: rgba(0,0,0,0.6);
  backdrop-filter: blur(4px);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 2000;
}
.button {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 20px;
}

.modal {
  width: 90%;
  max-width: 700px;
  border-radius: 20px;
  padding: 20px;
  box-shadow: 0 6px 20px rgba(0,0,0,0.2);
  background: #e8e8e8;
  @media (max-width: 375px) {
    width: 320px;
  }
}

.title {
  margin-bottom: 15px;
  text-align: left;
  font-family: var(--font-family);
font-weight: 600;
font-size: 12px;
color: #000;
margin-left: 20px;
}
.info {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 10px;
    text-align: center;
    color: #000;
}