    .modalOverlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,0.4);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 999;
    }

    .modal {
        background-color: #E8E8E8;
    width: 450px;
    padding: 20px;
    border-radius: 15px;
    position: relative;
    animation: fadeIn 0.3s ease;
    @media (max-width: 375px) {
        width: 300px
    }
}

    .title {
    font-family: var(--font-family);
    font-weight: 600;
    font-size: 14px;
    color: #000;
    text-align: left;

    }

    .closeBtn {
    position: absolute;
    right: 15px;
    top: 15px;
    background: none;
    border: none;
    font-size: 20px;
    cursor: pointer;
    }

    .list {
    display: flex;
    flex-direction: column;
    gap: 12px;
    }

    .item {
    display: grid;
    grid-template-columns: 120px 40px 90px 1fr;
    align-items: center;
    padding: 8px 5px;
    border-radius: 12px;
    background: #D9D9D9;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 14px;
    color: #000;
    @media (max-width: 375px) {
        grid-template-columns: 80px 30px 60px 1fr;
        font-size: 10px;
    }
    }
    .date {
    font-weight: 600;
    }

    .icon {
    width: 40px;
    }

    .temp {
    font-size: 16px;
    font-weight: 500;
    }

    .desc {
    font-size: 14px;
    text-transform: capitalize;
    }
