.overlay{
    display: flex;
    justify-content: center;
    align-items: center;
}
.modal{
    width: 634px;   
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    flex-wrap: wrap;
    border-radius: 15px;
    background: #e8e8e8;
    gap: 20px;
    padding-bottom: 20px;
    @media (max-width: 375px) {
        width: 293px;
        flex-direction: column;
    }
}          
.info{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 30px 0;
    border-radius: 10px;
    width: 177px;
    height: 200px;
    background: #d9d9d9;
}
.info_title{
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 12px;
    text-align: center;
    color: #000;
}
.info_data{
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    text-align: center;
    color: #000;
    margin-bottom: 28px
}